export function useSwapState(): AppState['swap'] {
  return useSelector<AppState, AppState['swap']>((state) => state.swap)
}

export function useSwapActionHandlers(): {
  onCurrencySelection: (field: Field, currency: Currency) => void
  onSwitchTokens: () => void
  onUserInput: (field: Field, typedValue: string) => void
  onUserInput2: (field: Field, typedValue2: string) => void
  onUserInputClaimable: (field: Field, typedValueClaimable: string) => void
  onUserInputNonClaimable: (field: Field, typedValueNonClaimable: string) => void
  onChangeRecipient: (recipient: string | null) => void
  onChangeEarnedRewards: (recipient: string | null) => void
  onChangePoolAmount: (recipient: string | null) => void
  onChangeIsClaimable: (isClaimable: string | null) => void
} {
  const dispatch = useDispatch<AppDispatch>()
  const onCurrencySelection = useCallback(
    (field: Field, currency: Currency) => {
      dispatch(
        selectCurrency({
          field,
          currencyId: currency instanceof Token ? currency.address : currency === ETHER ? 'BNB' : '',
        })
      )
    },
    [dispatch]
  )

  const onSwitchTokens = useCallback(() => {
    dispatch(switchCurrencies())
  }, [dispatch])

  const onUserInput = useCallback(
    (field: Field, typedValue: string) => {
      dispatch(typeInput({ field, typedValue }))
    },
    [dispatch]
  )

  const onUserInput2 = useCallback(
    (field: Field, typedValue2: string) => {
      dispatch(typeInput2({ field, typedValue2 }))
    },
    [dispatch]
  )

  const onUserInputClaimable = useCallback(
    (field: Field, typedValueClaimable: string) => {
      dispatch(typeInputClaimable({ field, typedValueClaimable }))
    },
    [dispatch]
  )

  const onUserInputNonClaimable = useCallback(
    (field: Field, typedValueNonClaimable: string) => {
      dispatch(typeInputNonClaimable({ field, typedValueNonClaimable }))
    },
    [dispatch]
  )

  const onChangeRecipient = useCallback(
    (recipient: string | null) => {
      dispatch(setRecipient({ recipient }))
    },
    [dispatch]
  )

  const onChangeEarnedRewards = useCallback(
    (earnedRewards: string | null) => {
      dispatch(setEarnedRewards({ earnedRewards }))
    },
    [dispatch]
  )

  const onChangePoolAmount = useCallback(
    (poolAmount: string | null) => {
      dispatch(setPoolAmount({ poolAmount }))
    },
    [dispatch]
  )

  const onChangeIsClaimable = useCallback(
    (isClaimable: string | null) => {
      dispatch(setIsClaimable({ isClaimable }))
    },
    [dispatch]
  )

  return {
    onSwitchTokens,
    onCurrencySelection,
    onUserInput,
    onUserInput2,
    onUserInputClaimable,
    onUserInputNonClaimable,
    onChangeRecipient,
    onChangeEarnedRewards,
    onChangePoolAmount,
    onChangeIsClaimable
  }
}

// try to parse a user entered amount for a given token
export function tryParseAmount(value?: string, currency?: Currency): CurrencyAmount | undefined {
  if (!value || !currency) {
    return undefined
  }
  try {
    const typedValueParsed = parseUnits(value, currency.decimals).toString()
    if (typedValueParsed !== '0') {
      return currency instanceof Token
        ? new TokenAmount(currency, JSBI.BigInt(typedValueParsed))
        : CurrencyAmount.ether(JSBI.BigInt(typedValueParsed))
    }
  } catch (error) {
    // should fail if the user specifies too many decimal places of precision (or maybe exceed max uint?)
    console.info(`Failed to parse input amount: "${value}"`, error)
  }
  // necessary for all paths to return a value
  return undefined
}

